import React, { useState } from "react";
import { Carousel } from "react-bootstrap";
import dota2 from "../img/dota-2.jpg";
import csgo from "../img/cs-go.jpg";
import hearthstone from "../img/hearthstone.jpg";
import overwatch from "../img/overwatch.png";

export default function GamesHero() {
  const [index, setIndex] = useState(0);

  const handleSelect = (selectedIndex, e) => setIndex(selectedIndex);

  return (
    <Carousel activeIndex={index} onSelect={handleSelect}>
      <Carousel.Item>
        <img src={dota2} alt="Dota 2" className="d-block w-100" />
      </Carousel.Item>
      <Carousel.Item>
        <img src={csgo} alt="CS GO" className="d-block w-100" />
      </Carousel.Item>
      <Carousel.Item>
        <img src={hearthstone} alt="Hearthstone" className="d-block w-100" />
      </Carousel.Item>
      <Carousel.Item>
        <img src={overwatch} alt="Overwatch" className="d-block w-100" />
      </Carousel.Item>
    </Carousel>
  );
}
