import React from "react";
import { Link } from "react-router-dom";
import logo from "../img/datto-gaming.png";
import { Navbar, Container, Nav, NavDropdown, Row, Col } from "react-bootstrap";

export default function NavBar() {
  return (
    <Navbar bg="light" expand="lg">
      <Container>
        <Link to="/" className="navbar-brand">
          <img src={logo} alt="Datto gaming" />
        </Link>
        <Navbar.Toggle aria-controls="gaming-nav" />
        <Navbar.Collapse id="gaming-nav">
          <Nav className="ml-auto">
            <Link to="/" className="nav-link">
              Home
            </Link>
            <NavDropdown title="Games" id="gaming-dropdown">
              <Container>
                <Row>
                  <Col lg="3" md="6">
                    <Link to="/dota2" className="dropdown-item">
                      Dota 2
                    </Link>
                    <span className="expanding"></span>
                  </Col>
                  <Col lg="3" md="6">
                    <Link to="/csgo" className="dropdown-item">
                      CS GO
                    </Link>
                    <span className="expanding"></span>
                  </Col>
                  <Col lg="3" md="6">
                    <Link to="/hearthstone" className="dropdown-item">
                      HearthStone
                    </Link>
                    <span className="expanding"></span>
                  </Col>
                  <Col lg="3" md="6">
                    <Link to="/overwatch" className="dropdown-item">
                      Overwatch
                    </Link>
                    <span className="expanding"></span>
                  </Col>
                </Row>
              </Container>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}
