import React from "react";
import "./App.css";
import NavBar from "./components/NavBar";
import { Route, Switch } from "react-router-dom";
import Home from "./pages/Home";
import Csgo from "./pages/Csgo";
import Dota2 from "./pages/Dota2";
import Hearthstone from "./pages/Hearthstone";
import Overwatch from "./pages/Overwatch";
import NotFound from "./pages/NotFound";

function App() {
  return (
    <>
      <NavBar />
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/dota2" component={Dota2} />
        <Route exact path="/csgo" component={Csgo} />
        <Route exact path="/hearthstone" component={Hearthstone} />
        <Route exact path="/overwatch" component={Overwatch} />
        <Route component={NotFound} />
      </Switch>
    </>
  );
}

export default App;
