import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";

function Dota2Heroes() {
  useEffect(() => {
    fetchHeroes();
  }, []);

  const [heroes, setHeroes] = useState([]);

  const fetchHeroes = async () => {
    const data = await fetch("https://api.opendota.com/api/heroes");

    const heroesList = await data.json();

    setHeroes(heroesList);
  };

  const heroesUrl = heroes.map(hero => {
    let heroesName = hero.localized_name;
    let newName = heroesName.replace(/ /g, "_");
    return `https://dota2.gamepedia.com/${newName}`;
  });

  const allRoles = new Set();

  heroes.map(hero => {
    allRoles.add(...hero.roles);
    return allRoles;
  });

  console.log(allRoles);
  console.log(heroes);

  return (
    <section className="dota2-heroes">
      <Container>
        <h1>dota 2 heroes</h1>
        <h3>find out more about your favorite heroes</h3>
        <Row>
          {heroes.map((hero, index) => {
            let heroesRoles = hero.roles.join(" ").toLowerCase();
            return (
              <Col
                className={`dota2-hero ${
                  hero.primary_attr
                } ${hero.attack_type.toLowerCase()} ${heroesRoles}`}
                key={hero.name}
                lg="2"
                md="3"
                sm="6"
                as="a"
                href={heroesUrl[index]}
              >
                {hero.localized_name}
              </Col>
            );
          })}
        </Row>
      </Container>
    </section>
  );
}

export default Dota2Heroes;
