import { Container, Row, Col } from "react-bootstrap";
import React, { Component } from "react";
import dotaIcon from "../img/dota-2-icon.png";
import csgoIcon from "../img/cs-go-icon.png";
import hearthstoneIcon from "../img/hearthstone-icon.png";
import overwatchIcon from "../img/overwatch-icon.png";

export default class AvailableGames extends Component {
  render() {
    const dataHome = require("../data-home.json");
    const listIcon = [dotaIcon, csgoIcon, hearthstoneIcon, overwatchIcon];
    return (
      <Container className="available-games">
        <h1>Available Online Games</h1>
        <hr />
        <Row>
          {dataHome.map((game, index) => {
            return (
              <Col lg="3" md="6" key={game.id}>
                <img src={listIcon[index]} alt="Game icons" />
                <h2>{game.title}</h2>
                <p>{game.description}</p>
                <a className={game.id} href={game.signUp}>
                  Sign up now
                </a>
              </Col>
            );
          })}
        </Row>
      </Container>
    );
  }
}
